*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;

  font-family: 'Poppins', sans-serif;
}

body{
  background: #0e1212;

  color: #fff;
}

ul{
  padding: 0;
}

li{
  list-style: none;
}

a{
  text-decoration: none;
}

.display{
  display: flex;
}

.flex_center{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}